import React, { useState, useRef } from 'react';
import { Modal, Form, Tooltip, Row, Col, Button, Input, Space, InputNumber, Select, message } from 'antd';
import { SearchOutlined, FileSyncOutlined } from '@ant-design/icons';
import { navigate } from 'gatsby';

import CustomerList from '../customer_list';
import UploadFile from '../upload_files';
import AxiosRequest from '../apis';
import OverallLoading from '../overall_loading';
import SelectSalePlatform from '../select_sale_platform';

const { Option } = Select;

const formInitObj = {
  customername: '',
  diopter_r: '',
  diopter_l: '',
  pupillary_distance: '',
  product_title: '',
  products_quantity: 1,
  purchase_amount: 0,
  sale_platform: 0,
  order_state: 0,
  receive_address: '',
  explains: "",
  upload: []
};

// 

const formItemLayout = {
  layout: 'horizontal',
  labelCol: {
    xs: { span: 12, },
    sm: { span: 10, },
  },
  wrapperCol: {
    xs: { span: 12, },
    sm: { span: 14, },
  },
};
const tailFormItemLayout = {
  wrapperCol: {
    xs: { span: 24, offset: 0, },
    sm: { span: 16, offset: 8, },
  },
};

const normFile = e => {
  console.log('Upload event:', e);
  if (Array.isArray(e)) {
    return e;
  }
  return e && e.fileList;
};

const CreatePurchaseRecords = () => {
  // 
  const [fileList, setFileList] = useState([]);

  const [spinning, setSpinning] = useState(false);
  const [form] = Form.useForm();
  const [showSelectCustomerModal, setShowSelectCustomerModal] = useState(false);
  const selectedCustomerObj = useRef({});

  const [requestCount, setRequestCount] = useState(0);
  const reqParams = useRef({});
  const reqObject = useRef({});
  const api = 'records-admin/v1/purchaserecord';
  const method = 'post';

  const responseDataHandle = (respObj) => {
    // respObj 
    console.log(respObj);
    setSpinning(false);
    if (respObj.code == 1000) {
      // 
      form.setFieldsValue({ ...formInitObj });
      setFileList([]);
      message.success('Success!');
      message.success('!');
    } else {
      message.error('Network Error...');
      message.error('...');
    }
  }

  const selectCustomerHandle = (customerObj) => {
    // 
    selectedCustomerObj.current = customerObj;
    const objKeys = Object.keys(customerObj);
    const nickName =
      objKeys.includes('name')
        ? customerObj.name
        : objKeys.includes('wx_nickname') ? customerObj.wx_nickname : '';
    form.setFieldsValue({
      customername: nickName,
    });
    setShowSelectCustomerModal(false);
  }

  const SelectOrderState = () => {
    // 0:, 1:, 2:
    return (
      <Select onChange={(val) => form.setFieldsValue({ order_state: Number(val) })} defaultValue="0">
        <Option value="0"></Option>
        <Option value="1"></Option>
        <Option value="2"></Option>
      </Select>
    )
  }

  const onFinish = values => {
    // 
    const deepcopyObj = { ...values };
    delete deepcopyObj.customername;
    const customerObj = { ...selectedCustomerObj.current };
    // id
    deepcopyObj.customer_id = Object.keys(customerObj).includes('customer_id')
      ? customerObj.customer_id
      : customerObj.id ? customerObj.id : 0;
    const imgLists = [].concat(deepcopyObj.upload);
    delete deepcopyObj.upload;
    // console.log(imgLists);
    for (let index = 0; index < imgLists.length; index++) {
      const resp = imgLists[index].response;
      let imgKey = 'image_cos_name' + (index + 1);
      deepcopyObj[imgKey] = resp.image_cos_name;
    }
    // 
    console.log(deepcopyObj);
    reqObject.current = deepcopyObj;
    setSpinning(true);
    setRequestCount(requestCount + 1);
  };

  return (
    <div>
      <OverallLoading spinning={spinning} />
      <h3></h3>
      <Form
        {...formItemLayout}
        form={form}
        name="createrecord"
        onFinish={onFinish}
        initialValues={{ ...formInitObj }}
        scrollToFirstError
      >
        <Form.Item label="" extra="">
          <Row gutter={8}>
            <Col span={18}>
              <Form.Item
                name="customername"
                noStyle
              >
                <Input disabled={true} />
              </Form.Item>
            </Col>
            <Col span={6}>
              <Tooltip title="">
                <Button type="primary" onClick={() => { setShowSelectCustomerModal(true) }} shape="circle" icon={<SearchOutlined />} />
              </Tooltip>
            </Col>
          </Row>
        </Form.Item>

        <Form.Item label=" R" name="diopter_r" extra="R">
          <Row gutter={8}>
            <Col span={18}>
              <Form.Item name="diopter_r" noStyle><Input /></Form.Item>
            </Col>
            <Col span={6}>
              <Tooltip title="">
                <Button type="primary" shape="circle" icon={<FileSyncOutlined />} />
              </Tooltip>
            </Col>
          </Row>
        </Form.Item>

        <Form.Item label=" L" extra="L">
          <Row gutter={8}>
            <Col span={18}>
              <Form.Item name="diopter_l" noStyle><Input /></Form.Item>
            </Col>
            <Col span={6}>
              <Tooltip title="">
                <Button type="primary" shape="circle" icon={<FileSyncOutlined />} />
              </Tooltip>
            </Col>
          </Row>
        </Form.Item>

        <Form.Item label="PD" name="pupillary_distance" ><Input /></Form.Item>
        <Form.Item label="" name="product_title" ><Input /></Form.Item>
        <Form.Item label="" name="products_quantity">
          <InputNumber min={0} max={1000}
            formatter={value => `${value}`.replace(/\B(?=(\d{3})+(?!\d))/g, ',')}
            parser={value => value.replace(/\s?|(,*)/g, '')} />
        </Form.Item>
        <Form.Item label="" name="purchase_amount">
          <InputNumber min={0} max={1000000}
            formatter={value => ` ${value}`.replace(/\B(?=(\d{3})+(?!\d))/g, ',')}
            parser={value => value.replace(/\s?|(,*)/g, '')} />
        </Form.Item>
        <Form.Item label="" name="sale_platform">
          <SelectSalePlatform />
        </Form.Item>
        <Form.Item label="" name="order_state"><SelectOrderState /></Form.Item>
        <Form.Item label="" name="receive_address" ><Input /></Form.Item>
        <Form.Item label="" name="explains" ><Input /></Form.Item>

        <Form.Item
          name="upload"
          label=""
          valuePropName="fileList"
          getValueFromEvent={normFile}
          extra=""
        ><UploadFile fileList={fileList} /></Form.Item>

        <Form.Item {...tailFormItemLayout}>
          <Space size="large">
            <Button type="primary" htmlType="submit"></Button>
            <Button type="dashed" onClick={() => { navigate('/'); }}></Button>
          </Space>
        </Form.Item>
      </Form>

      <AxiosRequest
        responseDataHandle={responseDataHandle} api={api} method={method}
        reqParams={reqParams.current} reqObject={reqObject.current} requestCount={requestCount}
      ></AxiosRequest>

      <Modal
        visible={showSelectCustomerModal}
        title=""
        footer={[
          <Button key="back" onClick={() => { setShowSelectCustomerModal(false); }}></Button>
        ]}
      ><CustomerList
        selectCustomerHandle={selectCustomerHandle}
        clickCreatedCustomerHandle={selectCustomerHandle} /></Modal>
    </div>
  )
}

export default CreatePurchaseRecords;
